#head {
  width: 1260px;
  margin: 75px auto 50px;
  height: 71px;
  position: relative; }
  #head .headBox {
    transition: all 2s;
    -webkit-transition: all 2s;
    transform: translate(0, 0);
    -webkit-transform: translate(0, 0);
    opacity: 1; }
  #head .hideHead {
    opacity: 0;
    transform: translate(0, -100px);
    -webkit-transform: translate(0, -100px); }
  #head .logo {
    height: 52.5px;
    width: 37.5px;
    float: left;
    display: block;
    line-height: 52.5px;
    padding: 0 60px 0 30px; }
    #head .logo img {
      height: 52.5px;
      width: 37.5px;
      vertical-align: middle; }
  #head .nav {
    height: 71px;
    float: left;
    width: 300px;
    line-height: 71px;
    font-size: 24px; }
    #head .nav a {
      text-decoration: none;
      color: #333;
      width: 60px;
      text-align: center;
      display: block;
      float: left;
      margin: 0 20px;
      position: relative; }
      #head .nav a:before {
        content: "";
        width: 0px;
        height: 2px;
        color: #333;
        position: absolute;
        left: 50%;
        margin-left: -0px;
        bottom: 10px;
        background: #333;
        transition: all 0.3s;
        -webkit-transition: all 0.3s; }
    #head .nav a:hover:before {
      width: 30px;
      margin-left: -15px; }
  #head .icon-sousuo {
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 28px;
    color: #333;
    cursor: pointer; }
